input[type='range'][data-v-37e80d5e] {
  -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
  background: transparent; /* Otherwise white in Chrome */

  width: 65%;
  height: 18px;
  margin: 0px; /* needed for firefox */
}
input[type='range'][data-v-37e80d5e]:focus {
  outline: none;
}
/* CHROME */
input[type='range'][data-v-37e80d5e]::-webkit-slider-thumb {
  -webkit-appearance: none;

  height: 11px;
  width: 11px;
  border-radius: 0px;
  background: rgb(68, 68, 68);
  border: 1px solid rgb(0, 255, 170);
  cursor: pointer;
  margin-top: -5px; /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic*/
}
input[type='range'][data-v-37e80d5e]::-webkit-slider-runnable-track {
  width: 100%;
  height: 1px;
  cursor: pointer;
  background: rgb(68, 68, 68);
  border-radius: 0px;
  border: 0px solid #010101;
  margin-top: 11px;
}
input[type='range'][data-v-37e80d5e]:focus::-webkit-slider-runnable-track {
  background: rgb(68, 68, 68);
}
/* FIREFOX */
input[type='range'][data-v-37e80d5e]::-moz-range-thumb {
  border: 1px solid rgb(0, 255, 170);
  height: 11px;
  width: 11px;
  border-radius: 0px;
  background: rgb(68, 68, 68);
  cursor: pointer;
}
input[type='range'][data-v-37e80d5e]::-moz-range-track {
  width: 100%;
  height: 1px;
  cursor: pointer;
  background: rgb(68, 68, 68);
  border-radius: 0px;
  border: none;
  margin-top: 11px;
}
